}

.dark-mode .btn-serachBar{
  color: #9aa0a6;
}

.dark-mode .fondo-duplicado p{
  color: #f1f1f1;
}

.dark-mode .fondo-duplicado h3{
  color: #f1f1f1;
}

.dark-mode .switch-btnSwitch{
  background-color: #1a2a31;
}

/* ================ MEDIA QUERIES (SMALL DEVIES) ================*/
@media screen and (max-width: 600px) {
  .dark-mode .switch-btnSwitch .switch__sun {
    display: block;
  }

