.switch-btnSwitch{
    background-color: #141f24;
    border-radius: 500px;
    border: none;
    position: relative;
    cursor: pointer;
    display: flex;
    outline: none;
    margin-top: 7px;
    margin-left: 5px;
    height: 30px;
}

.switch-btnSwitch::after{
    content: "";
    display: block;
    width: 30px;
    height: 30px;
    position: absolute;
    background: #f1f1f1;
    top: 0;
    left: 0;
    right: unset;
    border-radius: 100px;
    transition: .5s ease all;
    box-shadow: 0px 0xp 2px rgbs(0,0,0,.2);
}

.switch-btnSwitch span{
    width: 27px;
    height: 0;
    display: block;
    background: none;
    color: #fff;
}

/* ================ MEDIA QUERIES (SMALL DEVIES) ================*/
@media screen and (max-width: 600px) {
    .switch-btnSwitch {
        border-radius: 50%;
        /* margin-bottom: 15px; */
    }
    
    .switch-btnSwitch .switch__sun {
       display: none; 
    }

    .switch-btnSwitch::after {
        display: none;
    }
}
